import QRCode from "qrcode";
export default {
  data() {
    return {
      showCode: false,
      showZfbCode: false,
      showCanvas: false,
      isRotate: false,
    };
  },
  created() {},
  mounted() {
    console.log(window);
    var canvas = document.getElementById("canvas");
    QRCode.toCanvas(canvas, window.location.href, function(error) {
      if (error) console.error(error);
      console.log("success!");
    });
  },
  methods: {}
};
